<template>
  <div id="man">
    <Menu />
      <div class="blockBar">
        <div class="container">
          <img class="responsive-img" src="@/assets/images/hr.png" alt="">
          <h4 class="titleMain">Collection 2020 for man</h4>
          <Loader v-if="!clotheIs"/>
          <div v-else class="tabsWrp">
            <tabs :options="{useUrlFragment: false}">
              <tab name ="All clothes &#9658;" class="tabb">
                  <div class="offersMain wrpClotheBar">
                      <ClotheItem
                      class="product_bar_item"
                      v-for="item in newClothe"
                      :key="item.index"
                      v-show="item.sex == 'male'"
                      v-bind:productdata="item"
                      />
                  </div>
              </tab>
              <tab name="New collection &#9658;">
                  <div class="offersMain wrpClotheBar">
                      <ClotheItem
                      class="product_bar_item"
                      v-for="item in newClothe"
                      :key="item.index"
                       v-show="item.type == 'dress' && item.sex == 'male'"
                      v-bind:productdata="item"
                      />
                  </div>
              </tab>
              <tab id="oh-hi-mark" name="Ofice looks &#9658;">
                  <div class="offersMain wrpClotheBar">
                      <ClotheItem
                      class="product_bar_item"
                      v-for="item in newClothe"
                      :key="item.index"
                      v-show="item.type == 'oficelooks' && item.sex == 'male'"
                      v-bind:productdata="item"
                      />
                  </div>
              </tab>
              <tab name="Discaunts &#9658;">
                    <div class="offersMain wrpClotheBar">
                      <ClotheItem
                      class="product_bar_item"
                      v-for="item in newClothe"
                      :key="item.index"
                      v-show="item.oferta && item.sex == 'male'"
                      v-bind:productdata="item"
                      />
                  </div>
              </tab>
            </tabs>
          </div>
        </div>
      </div>
    <Footer />
  </div>
</template>

<script>
import Menu from '@/components/Menu.vue'
import Footer from '@/components/Footer.vue'
import axios from 'axios'
import ClotheItem from '@/components/ClotheItem.vue'

const baseUrl = "http://localhost:3000/newClothes"

export default {
  name: 'Man',
  components: {
      Menu,
      Footer,
      ClotheItem
  },
  data () {
    return {
      newClothe: [],
      cardopen: "/opencard/",
      idcurrency: '',
      clotheIs: false
    }
  },
  async created() {
    try{
      const res = await axios.get(baseUrl);
      this.clotheIs = true
      this.newClothe = res.data;
    } catch (e) {
      console.error(e);
    }
  },
  methods: {
    currentid() {
      console.log(this.idcurrency)
    }
  }
}
</script>

